<template>
  <article class="veui-form-demo">
    <h1><code>&lt;veui-form&gt;</code></h1>
    <section>
      <h2>datafieldv-modelform</h2>
      <veui-form ref="form1" :data="storeData1">

        <veui-field label="" field="nickName">
          <veui-input v-model="storeData1.nickName"></veui-input>
        </veui-field>

        <veui-field label="" field="sex">
          <veui-select :options="storeData1.sexItems" v-model="storeData1.sex"></veui-select>
        </veui-field>

        <veui-field label="" field="married">
          <veui-radioboxgroup :items="storeData1.marryItems" v-model="storeData1.married"></veui-radioboxgroup>
        </veui-field>

        <veui-field label="" field="habit">
          <veui-checkboxgroup type="checkbox" :items="storeData1.habitItems" v-model="storeData1.habit"></veui-checkboxgroup>
        </veui-field>

        <veui-field label="" field="birthday">
          <veui-datepicker v-model="storeData1.birthday"></veui-datepicker>
        </veui-field>

        <veui-field label="" field="avatar">
          <veui-uploader uploaderType="image"
            action="/upload"
            request-mode="xhr"
            ui="multiline vertical bottom-mask list-icon"
            :disabled="false"
            :max-count="1"
            :value="storeData1.avatar"
            :max-size="10"
            preview-image
            needButton
            extention-types="jpg,jpeg,png"></veui-uploader>
          <!-- <p class="output">{{ outputData.avatar }}</p> -->
        </veui-field>

        <div class="operation">
          <veui-button @click="() => this.$refs.form1.reset()"></veui-button>
        </div>
      </veui-form>
    </section>
    <section>
      <h2></h2>
      <veui-form>

        <veui-fieldset class="two-name" label="">
          <veui-field>
            <veui-input placeholder="" v-model="storeData2.lastName"></veui-input>
          </veui-field>

          <veui-field>
            <veui-input placeholder="" v-model="storeData2.firstName"></veui-input>
          </veui-field>
        </veui-fieldset>

        <veui-fieldset label="">
          <veui-field>
            <veui-select v-model="storeData2.phoneType" :options="storeData2.phoneTypeOptions"></veui-select>
          </veui-field>

          <veui-field>
            <veui-input placeholder="" v-model="storeData2.phone"></veui-input>
          </veui-field>
        </veui-fieldset>

        <veui-fieldset class="salary" tip=" <veui-span> ">
          <template slot="label">
            <veui-label></veui-label>
          </template>
          <veui-field>
            <veui-input v-model="storeData2.start"></veui-input>
          </veui-field>
          <veui-span>-</veui-span>
          <veui-field>
            <veui-input v-model="storeData2.end"></veui-input>
          </veui-field>
          <veui-span></veui-span>
        </veui-fieldset>
      </veui-form>
    </section>
    <section>
      <h2></h2>
      <veui-form ui="inline">

        <veui-fieldset class="left">
          <veui-field label="">
            <veui-select v-model="storeData3.statusSelected" :options="storeData3.statusOptions"></veui-select>
          </veui-field>

          <veui-field label="">
            <veui-datepicker range v-model="storeData3.range"></veui-datepicker>
          </veui-field>
        </veui-fieldset>

        <veui-fieldset class="right">
          <veui-field>
            <veui-searchbox placeholder=""></veui-searchbox>
          </veui-field>
        </veui-fieldset>

      </veui-form>

      <veui-form ui="inline">

        <veui-fieldset class="left" ui="alt">
          <veui-field label="">
            <veui-select ui="alt" v-model="storeData3.statusSelected" :options="storeData3.statusOptions"></veui-select>
          </veui-field>

          <veui-field label="">
            <veui-datepicker range ui="alt" v-model="storeData3.range"></veui-datepicker>
          </veui-field>
        </veui-fieldset>

        <veui-fieldset class="right">
          <veui-field>
            <veui-searchbox ui="alt" placeholder=""></veui-searchbox>
          </veui-field>
        </veui-fieldset>

      </veui-form>
    </section>
    <section>
      <h2></h2>
      <veui-form disabled :data="storeData1">

        <veui-field label="" field="nickName">
          <veui-input v-model="storeData1.nickName"></veui-input>
        </veui-field>

        <veui-field label="" field="sex">
          <veui-select :options="storeData1.sexItems" v-model="storeData1.sex"></veui-select>
        </veui-field>

        <veui-field label="" field="married">
          <veui-radioboxgroup :items="storeData1.marryItems" v-model="storeData1.married"></veui-radioboxgroup>
        </veui-field>

        <veui-field label="" field="habit">
          <veui-checkboxgroup type="checkbox" :items="storeData1.habitItems" v-model="storeData1.habit"></veui-checkboxgroup>
        </veui-field>

        <veui-field label="" field="birthday">
          <veui-datepicker v-model="storeData1.birthday"></veui-datepicker>
        </veui-field>

        <veui-field label="" field="avatar">
          <veui-uploader uploaderType="image"
            action="/upload"
            request-mode="xhr"
            ui="multiline vertical bottom-mask list-icon"
            :disabled="false"
            :max-count="1"
            :value="storeData1.avatar"
            :max-size="10"
            preview-image
            needButton
            extention-types="jpg,jpeg,png"></veui-uploader>
          <!-- <p class="output">{{ outputData.avatar }}</p> -->
        </veui-field>
      </veui-form>
      <h2></h2>
      <veui-form>
        <veui-fieldset disabled class="two-name" label="">
          <veui-field>
            <veui-input placeholder="" v-model="storeData2.lastName"></veui-input>
          </veui-field>

          <veui-field>
            <veui-input placeholder="" v-model="storeData2.firstName"></veui-input>
          </veui-field>
        </veui-fieldset>

        <veui-fieldset label="">
          <veui-field>
            <veui-select v-model="storeData2.phoneType" :options="storeData2.phoneTypeOptions"></veui-select>
          </veui-field>

          <veui-field readonly>
            <veui-input placeholder="" v-model="storeData2.phone"></veui-input>
          </veui-field>
        </veui-fieldset>

        <veui-fieldset class="salary" tip=" <veui-span> ">
          <template slot="label">
            <veui-label></veui-label>
          </template>
          <veui-field>
            <veui-input v-model="storeData2.start"></veui-input>
          </veui-field>
          <veui-span>-</veui-span>
          <veui-field>
            <veui-input disabled v-model="storeData2.end"></veui-input>
          </veui-field>
          <veui-span></veui-span>
        </veui-fieldset>
      </veui-form>
    </section>
    <section>
      <h2> field  name </h2>
      <veui-form
        ref="form2"
        @submit="submit"
        @invalid="handleInvalid"
        :readonly="isValidating"
        :data="storeData4"
        :validators="validators"
        :beforeValidate="beforeValidate"
        :afterValidate="afterValidate">

        <veui-field field="name" name="name1" rules="required" label="" tip="">
          <veui-input v-model="storeData4.name"></veui-input>
        </veui-field>

        <veui-field field="name" name="name2" :rules="dynamicNameRule" label="" tip="blur">
          <veui-input placeholder="2" v-model="storeData4.name"></veui-input>
        </veui-field>

        <veui-field field="age" name="age1" :rules="ageRule" label="">
          <veui-input placeholder=", 3" v-model="storeData4.age"></veui-input>
        </veui-field>

        <veui-field field="desc" name="desc" rules="required" label="">
          <veui-input rows="3" type="textarea" v-model="storeData4.desc"></veui-input>
        </veui-field>

        <veui-fieldset name="phoneSet" label="" :required="true">
          <veui-field field="phoneType" name="phoneType">
            <veui-select v-model="storeData4.phoneType" :options="storeData4.phoneTypeOptions"></veui-select>
          </veui-field>

          <veui-field field="phone" name="phone" rules="numeric required">
            <veui-input v-model="storeData4.phone"></veui-input>
          </veui-field>
        </veui-fieldset>

        <veui-field field="habit" name="habit" :rules="habitRule" label="" tip="">
          <veui-checkboxgroup type="checkbox" :items="storeData4.habitItems" v-model="storeData4.habit"></veui-checkboxgroup>
        </veui-field>

        <veui-fieldset label="" class="salary" tip="" :required="true">
          <veui-field field="start" name="start" :rules="numRequiredRule" class="start-field">
            <veui-input v-model="storeData4.start"></veui-input>
          </veui-field>
          <veui-span>-</veui-span>
          <veui-field field="end" name="end" :rules="numRequiredRule">
            <veui-input v-model="storeData4.end"></veui-input>
          </veui-field>
          <veui-span></veui-span>
        </veui-fieldset>

        <veui-field field="protocol" name="protocol" :rules="protocolRequiredRule" label="">
          <veui-checkbox falseValue="" v-model="storeData4.protocol"></veui-checkbox>
        </veui-field>

        <div class="operation">
          <veui-button ui="primary" :loading="isValidating" type="submit"></veui-button>
          <veui-button @click="() => this.$refs.form2.reset()" :loading="isValidating"></veui-button>
        </div>
      </veui-form>
    </section>
    <section>
      <h2></h2>
      <veui-form
        @submit="submit"
        @invalid="handleInvalid"
        :data="storeData5"
        :validators="qindianValidator"
        :beforeValidate="beforeValidate"
        :afterValidate="afterValidate">

        <veui-field field="qindian" label="" name="qindian">
          <veui-input v-model="storeData5.qindian"></veui-input>
        </veui-field>

        <veui-fieldset v-for="(item, index) in storeData5.scheduleInfo" :key="index" :required="true">
          <veui-field
            :field="`scheduleInfo[${index}].project`"
            :name="'projectName' + (index + 1)"
            :rules="requiredRule"
            :label="`-${index + 1}`">
            <veui-input placeholder="" v-model="item.project"></veui-input>
          </veui-field>
          <veui-field
            :field="`scheduleInfo[${index}].range`"
            :name="`schedule${index + 1}`"
            :rules="requiredRule">
            <veui-datepicker v-model="item.range" range></veui-datepicker>
          </veui-field>
          <veui-button @click="dynamicDelete(index)"></veui-button>
        </veui-fieldset>

        <div class="operation">
          <veui-button ui="primary" :loading="isValidating" type="submit"></veui-button>
          <veui-button @click="dynamicAdd"></veui-button>
        </div>
      </veui-form>
    </section>
  </article>
</template>

<script>
import {
  Form, Fieldset, Field, Label, Span, Input,
  Button, DatePicker, Uploader, Select,
  Checkbox, CheckboxGroup, RadioboxGroup, Searchbox
} from 'veui'
import moment from 'moment'
import bus from '../bus'

export default {
  name: 'demo-form',

  components: {
    'veui-span': Span,
    'veui-input': Input,
    'veui-button': Button,
    'veui-form': Form,
    'veui-fieldset': Fieldset,
    'veui-field': Field,
    'veui-label': Label,
    'veui-datepicker': DatePicker,
    'veui-uploader': Uploader,
    'veui-select': Select,
    'veui-checkbox': Checkbox,
    'veui-checkboxgroup': CheckboxGroup,
    'veui-radioboxgroup': RadioboxGroup,
    'veui-searchbox': Searchbox
  },

  data () {
    let habit = ['']
    let habitItems = [
      {
        value: '', label: ''
      },
      {
        value: '', label: ''
      },
      {
        value: '', label: ''
      },
      {
        value: '', label: ''
      }
    ]
    let phoneType = 'mobile'
    let phoneTypeOptions = [
      {
        label: '',
        value: 'phone'
      },
      {
        label: '',
        value: 'mobile'
      }
    ]
    return {
      storeData1: {
        nickName: '',
        sex: '',
        married: false,
        marryItems: [
          {
            value: true, label: ''
          },
          {
            value: false, label: ''
          }
        ],
        sexItems: [
          {
            value: '-', label: ''
          },
          {
            value: '', label: ''
          },
          {
            value: '', label: ''
          }
        ],
        habit,
        habitItems,
        birthday: new Date(),
        avatar: [{ src: 'https://www.baidu.com/img/bd_logo1.png' }]
      },
      storeData2: {
        lastName: '',
        firstName: '',
        habit,
        habitItems,
        phone: '18888888888',
        phoneType,
        phoneTypeOptions,
        start: null,
        end: null
      },
      storeData3: {
        statusSelected: 1,
        statusOptions: [
          {
            label: '1',
            value: 1
          },
          {
            label: '2',
            value: 2
          },
          {
            label: '3',
            value: 3
          },
          {
            label: '4',
            value: 4
          }
        ],
        range: [moment().toDate(), moment().add(3, 'month').toDate()]
      },
      storeData4: {
        name: '',
        age: null,
        desc: '',
        habit,
        habitItems,
        phone: '18888888888',
        phoneType,
        phoneTypeOptions,
        start: null,
        end: null,
        protocol: ''
      },
      requiredRule: [
        {
          name: 'required',
          value: true,
          triggers: 'blur,input'
        }
      ],
      numRequiredRule: [
        {
          name: 'numeric',
          value: true,
          triggers: 'blur,input'
        },
        {
          name: 'required',
          value: true,
          triggers: 'blur,input'
        }
      ],
      protocolRequiredRule: [
        {
          name: 'required',
          value: true,
          message: '',
          triggers: 'change'
        }
      ],
      dynamicNameRule: [
        {
          name: 'required',
          value: true,
          triggers: 'blur,input'
        },
        {
          name: 'minLength',
          value: 2
        }
      ],
      ageRule: [
        {
          name: 'required',
          value: true,
          triggers: 'blur,input'
        },
        {
          name: 'numeric',
          value: true,
          triggers: 'blur,input'
        },
        {
          name: 'maxLength',
          value: 3,
          triggers: 'change'
        }
      ],
      habitRule: [
        {
          name: 'minLength',
          value: 3,
          message: '',
          triggers: 'change'
        }
      ],
      isValidating: false,
      validators: [
        {
          fields: ['start', 'end'],
          handler (start, end) {
            if (start == null || end == null) {
              return true
            }

            if (parseInt(start, 10) >= parseInt(end, 10)) {
              return {
                start: ''
              }
            }
            return true
          },
          triggers: ['change', 'submit,input']
        },
        {
          fields: ['phone'],
          handler (phone) {
            return new Promise(function (resolve) {
              setTimeout(function () {
                let res
                if (phone === '18888888888') {
                  res = {
                    phoneSet: ''
                  }
                }
                return resolve(res)
              }, 3000)
            })
          }
        }
      ],
      beforeValidate () {
        bus.$emit('log', 'beforeValidate')
        this.isValidating = true
      },
      afterValidate () {
        bus.$emit('log', 'afterValidate')
        this.isValidating = false
      },
      storeData5: {
        qindian: 'Evan You',
        scheduleInfo: [
          {
            project: 'vuejs',
            range: [moment().toDate(), moment().add(3, 'month').toDate()]
          }
        ]
      },
      qindianValidator: [
        {
          fields: ['qindian', 'qindian'],
          handler (qindian) {
            if (qindian !== 'Evan You') {
              return {
                qindian: ''
              }
            }
            return true
          },
          triggers: ['input', 'submit']
        }
      ]
    }
  },

  methods: {
    handleInvalid (e) {
      bus.$emit('log', 'handleInvalid', e)
      this.isValidating = false
    },
    submit (data, e) {
      bus.$emit('log', 'submit', data, e)
    },
    dynamicAdd () {
      this.storeData5.scheduleInfo.push({
        project: null,
        range: null
      })
    },
    dynamicDelete (index) {
      this.storeData5.scheduleInfo.splice(index, 1)
    }
  }
}
</script>

<style lang="less">
@import "~veui-theme-dux/lib.less";

.veui-form-demo {
  h2 {
    margin-bottom: 40px;
  }

  section + section {
    margin-top: 50px;
  }

  margin-bottom: 50px;

  .veui-form[ui~="inline"] + .veui-form[ui~="inline"] {
    margin-top: 30px;
  }

  .left {
    float: left;
  }

  .right {
    float: right;
  }

  .output {
    position: absolute;
    display: inline-block;
    left: 560px;
    line-height: 36px;
    margin: 0 0 0 50px;

    &::before {
      position: absolute;
      left: -80px;
      content: "";
      line-height: 32px;
      font-size: 30px;
      color: #999;
    }
  }

  .two-name {
    .veui-input {
      width: 75px;
    }
  }

  .salary {
    .veui-input {
      width: 67px;
    }
  }

  .veui-uploader-list-image li {
    margin: 0;
  }

  .start-field {
    .veui-field-error:first-of-type {
      overflow: hidden;
      text-overflow: ellipsis;
      width: 80px;
      white-space: nowrap;
    }
  }

  .operation {
    margin-top: 60px;
    margin-left: 120px;

    [class*="veui"] {
      margin-left: 10px;
    }

    [class*="veui"]:first-child {
      margin-left: 0;
    }
  }
}
</style>



